.App {
  text-align: center;
  scroll-behavior: smooth;
}

.App-logo {
  height: 35vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 14s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.userRatingInput li {
  display: inline-block;

}
.userRatingInput ul {
 padding:0;

}
.reviewSubmitButton {
  font-size: 1.2rem;
}

.TagLine {
  font-family: "Josefin Slab", serif;
  font-size: 1.4em;
  margin-bottom: 0.4em;
}

.MainLogoText {
  font-family: "Monoton", cursive;
  font-weight: normal;
  color: cornsilk;
  text-shadow: 2px 2px #ff0000b0;
  margin-bottom: 0.2em;
}

.RestaurantList {
  font-family: "Lobster", cursive;
  font-size: 1.2em;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 80%;
}

.restaurantItem {
  border: solid;
}

.AddMessage {
  color: #282c34;
  font-size: smaller;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  position: absolute;
  bottom: 0;
  left: 0;
  margin: 0.3rem;
  background-color: #d5ffc2;
  border-radius: 5px;
}
.addReviewOption {
  border-radius: 20px;
  width:80%;
  margin: 0 auto;
  background-color: cornsilk;
  padding: 10px 0;
  border: solid #282c34 2px;
  font-family: "ABeeZee", sans-serif;
  font-size: 1.2rem;
  align-items: center;
}

 .addReviewMessage {
  display: block;
}
.labelReviewText {
  text-align: center;
}

.priceFilter li {
  display: inline-block;
}

.priceButton {
  padding: 5px 15px;
  border: solid black 1px;
  background-color: #d5ffc2;
}

.FilterContainer {
  width: 98.5%;
  border: 3px black solid;
  font-family: "ABeeZee", sans-serif;
  background-color: cornsilk;
}

.RestaurantReview,
.modalPadding,
.inputPadding {
  width: 75%;
  margin: 0 auto;
  background-color: #d5ffc2;
}
.modalPadding,
.inputPadding {
  height: 600px;
  overflow-y: scroll;
  overflow-x: hidden;
}

#LobsterH1 {
  font-family: "Lobster", cursive;
  font-size:2.4rem;
}
.labelText {
  font-size: larger;
  float:left;
  margin-left: 20%;
  padding:4px;
}
#rName, #rAddress {
  float:right;
  margin-right: 20%;
  width:30%;
  padding: 4px;
 
}
.RestaurantReview {
  height: 75%;
  align-items: center;
}

blockquote {
  border-radius: 20px;
  background-color: cornsilk;
  padding: 10px;
  border: solid #282c34 2px;
  font-family: "ABeeZee", sans-serif;
  font-size: 1.2rem;
}

.modalClose,
.inputClose {
  width: 5%;
  margin: 10px;
  float: right;
  border-radius: 6px;
  background-color: red;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.modalImage {
  height: 200px;
  border-radius: 10px;
  border: solid #282c34 5px;
}

.ratingStars {
  width: 10rem;
}

.mapBox {
  background-color: lightskyblue;
  width: 100%;
  height: 1000px;
}
.inputBody {
  font-family: "Josefin Slab", serif;
}

.Modal,
.Input {
  z-index: 100000000000000;
  display: block;
  position: absolute;
  padding-top: 2%;
  border-radius: 25px;
  width: 65%;
}

.ModalHeader {
  margin: 10px;
  font-size: 2rem;
}

.ModalItemTitle {
  font-family: "Monoton", cursive;
  font-size: 1.8rem;
  font-weight: bolder;
}

.itemDivider {
  width: 75%;
  border: 3px solid green;
  border-radius: 3px;
}

.Main {
  float: left;
  width: 70%;
  height: 90%;
}

.Aside {
  float: right;
  width: 35%;
  height: 625px;
  background-color: lightskyblue;
}

.AsideAddress {
  font-family: "Josefin Slab", serif;
}

p {
  color: #282c34;
}

ul {
  list-style-type: none;
  margin-top: 2px;
}
li {
  padding: 0 8px;
  display: inline;
  margin-bottom: 5px;
}

.container {
  display: flex;
}
#ratingDisclaimer {
  position: relative;
  bottom: 0px;
  padding: 10px;
  font-family: "ABeeZee", sans-serif;
}
